.progressContainer{
    background-color: rgb(196, 196, 196);
    border-radius: 20px;
    height: 30px;
    width: 900px;
    margin-top: 20px;
}
.progressBar{
    height: 100%;
    width: 0%;
    background-color: rgb(0, 0, 0);
    border-radius: 20px;
    opacity:1;
    transition: 0.5s;
}
